<template>
    <div class="view-container">

        <!---->
        <div class="row breadcrumb-container">
            <el-breadcrumb separator="/">
                <el-breadcrumb-item></el-breadcrumb-item>
            </el-breadcrumb>
        </div>

        <!--  -->
        <div class="row ope-container">
            <el-button type="success" size="small" @click="addVideo()"></el-button>
        </div>

        <!--  -->
        <div class="row data-container">
            <el-table :data="videoDate" border style="width: 100%" :max-height="tabMaxHeight">
                <el-table-column label="ID" prop="id">
                </el-table-column>
                <el-table-column label="" prop="categoryName">
                </el-table-column>
                <el-table-column label="" prop="name">
                </el-table-column>
                <el-table-column label="" prop="originName">
                </el-table-column>
                <el-table-column label="" prop="duration">
                    <template slot-scope="scope">
                        <div>{{Math.floor((scope.row.duration/ 3600)%24)}}  {{Math.floor((scope.row.duration / 60) % 60)}}  {{Math.floor(scope.row.duration % 60) }}</div>
                    </template>
                </el-table-column>
                <el-table-column label="" prop="fileSize">
                </el-table-column>
                <el-table-column label="" prop="createTime">
                    <template slot-scope="scope">
                        <div>{{moment(scope.row.createTime)}}</div>
                    </template>
                </el-table-column>
                <el-table-column label="" prop="updateTime">
                    <template slot-scope="scope">
                        <div v-if="scope.row.updateTime">{{moment(scope.row.updateTime)}}</div>
                    </template>
                </el-table-column>
                <el-table-column label="" width="200">
                    <template slot-scope="scope">
                        <el-button type="primary" size="mini" @click="editVideoItem(scope.row)">
                            <i class="el-icon-edit"></i>
                        </el-button>
                        <el-button type="primary" size="mini" @click="detailVideoItem(scope.row)">
                            <i class="el-icon-tickets"></i>
                        </el-button>
                        <el-button type="danger" size="mini" @click="delVideoItem(scope.row)">
                            <i class="el-icon-delete"></i>
                        </el-button>
                    </template>
                </el-table-column>

            </el-table>
        </div>

        <!--  -->
        <el-dialog title="" class="add-video" :visible.sync="addVideoDialogVisible" width="30%" :before-close="handleClose">
            <el-form label-position="left" label-width="120px" :model="addVideoData">
                <el-form-item label="" prop="">
                    <el-select v-model="addVideoData.categoryId" placeholder="">
                        <el-option v-for="item in categoryData" :key="item.name" :label="item.name" :value="item.id">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="" prop="">
                    <el-input v-model="addVideoData.name"></el-input>
                </el-form-item>
                <el-form-item label="" prop="">
                    <el-input v-model="addVideoData.introduction"></el-input>
                </el-form-item>
                <el-form-item label="" prop="">
                    <el-upload class="upload-demo" action="http://47.95.250.247/admin-api/file/video/upload" :on-success="handleSuccessVideo" :headers="headers">
                        <el-button size="small" type="primary"></el-button>
                        <div slot="tip" class="el-upload__tip">mp4  1G</div>
                    </el-upload>
                </el-form-item>
                <el-form-item label="" prop="">
                    <el-upload class="upload-demo" action="http://47.95.250.247/admin-api/file/image/upload" :headers="headers" :on-success="handleSuccessPic">
                        <el-button size="small" type="primary"></el-button>
                        <div slot="tip" class="el-upload__tip">jpg/png  800K800*600</div>
                    </el-upload>
                </el-form-item>
                <el-form-item label="" prop="" class="video-druction">
                    <el-input v-model="duration.hour" size="small" min='0' max="59" type="number"></el-input> 
                    <el-input v-model="duration.minute" size="small" min='0' max="59" type="number"></el-input> 
                    <el-input v-model="duration.second" size="small" min='0' max="59" type="number"></el-input> 
                </el-form-item>

            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button size="small" @click="addVideoDialogVisible = false"> </el-button>
                <el-button type="primary" size="small" @click="submitAddVideo()"> </el-button>
            </span>
        </el-dialog>

        <!--  -->
        <el-dialog title="" class="add-video" :visible.sync="editVideoDialogVisible" width="30%" :before-close="handleClose">
            <el-form label-position="left" label-width="80px" :model="editVideoData">
                <el-form-item label="" prop="">
                    <el-select v-model="editVideoData.categoryId" placeholder="">
                        <el-option v-for="item in categoryData" :key="item.name" :label="item.name" :value="item.id">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="" prop="name">
                    <el-input v-model="editVideoData.name"></el-input>
                </el-form-item>
                <el-form-item label="" prop="introduction">
                    <el-input v-model="editVideoData.introduction"></el-input>
                </el-form-item>
                <el-form-item label="" prop="">
                    <el-upload class="upload-demo" action="http://47.95.250.247/admin-api/file/video/upload" :on-success="handleSuccessVideoEdit" :headers="headers">
                        <el-button size="small" type="primary"></el-button>
                        <div slot="tip" class="el-upload__tip">mp4  1G</div>
                    </el-upload>
                </el-form-item>
                <el-form-item label="" prop="">
                    <el-upload class="upload-demo" action="http://47.95.250.247/admin-api/file/image/upload" :headers="headers" :on-success="handleSuccessPicEdit">
                        <el-button size="small" type="primary"></el-button>
                        <div slot="tip" class="el-upload__tip">jpg/png  800K800*600</div>
                    </el-upload>
                </el-form-item>
                <el-form-item label="" prop="" class="video-druction">
                    <el-input v-model="duration.hour" size="small" min='0' max="59" type="number"></el-input> 
                    <el-input v-model="duration.minute" size="small" min='0' max="59" type="number"></el-input> 
                    <el-input v-model="duration.second" size="small" min='0' max="59" type="number"></el-input> 
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button size="small" @click="editVideoDialogVisible = false"> </el-button>
                <el-button type="primary" size="small" @click="submitEditVideo()"> </el-button>
            </span>
        </el-dialog>

        <!--  -->
        <el-dialog title="" class="add-video" :visible.sync="delVideoDialogVisible" width="30%" :before-close="handleClose">
            <span></span>
            <span slot="footer" class="dialog-footer">
                <el-button size="small" @click="delVideoDialogVisible = false"> </el-button>
                <el-button type="danger" size="small" @click="submitDelVideo()"> </el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
import baseService from '../../service/baseService.js'
import { getSessionStorage } from '../../utils/base.js'
import moment from 'moment'
export default {
  name: 'video-list',
  data() {
    return {
      headers: {
        authKey: getSessionStorage('authKey')
      },
      // 
      videoDate: [],
      tabMaxHeight: 0,
      //   
      addVideoDialogVisible: false,
      //   
      addVideoData: {
        categoryId: '',
        name: '',
        introduction: '',
        videoToken: '',
        thumbToken: '',
        duration: 0
      },
      //   
      editVideoDialogVisible: false,
      //   
      editVideoData: {
        id: '',
        categoryId: '',
        name: '',
        introduction: '',
        videoToken: '',
        thumbToken: '',
        duration: 0
      },
      duration: {
        hour: 0,
        minute: 0,
        second: 0
      },
      //
      delVideoDialogVisible: false,
      //   ID
      delVideoId: '',
      //   
      categoryData: [],
      videoPath: {
        getPath: '/video/page',
        addPath: '/video/add',
        editPath: '/video/update',
        delPath: '/video/delete'
      },
      categoryPath: {
        getPath: '/category/video'
      }
    }
  },
  mounted() {
    this.getVideoDate()
  },
  methods: {
    // 
    moment(time) {
      return moment(time).format('YYYY-MM-DD h:mm:ss')
    },
    //   
    getVideoDate() {
      baseService
        .basePostData(this.videoPath.getPath, {
          pageNum: this.currentPage,
          pageSize: this.pageSize
        })
        .then(res => {
          let result = res.data.data
          this.videoDate = result.list
          this.totalSize = result.total
          let clientHieght = document.body.clientHeight
          this.tabMaxHeight = clientHieght - 60 - 30 - 30 - 50 - 50
        })
    },
    //   
    addVideo() {
      this.addVideoData = {
        categoryId: '',
        name: '',
        introduction: '',
        videoToken: '',
        thumbToken: '',
        duration: 0
      }
      this.duration = {
        hour: 0,
        minute: 0,
        second: 0
      }
      //
      baseService.basePostData(this.categoryPath.getPath).then(res => {
        let result = res.data.data
        this.categoryData = result
      })
      this.addVideoDialogVisible = true
    },
    // 
    submitAddVideo() {
      this.addVideoData.duration = this.duration.hour * 60 * 60 + this.duration.minute * 60 + this.duration.second
      this.addVideoDialogVisible = false
      baseService.basePostData(this.videoPath.addPath, this.addVideoData).then(res => {
        this.getVideoDate()
      })
    },
    //
    delVideoItem(row) {
      this.delVideoId = row.id
      this.delVideoDialogVisible = true
    },
    // 
    submitDelVideo() {
      this.delVideoDialogVisible = false
      baseService.baseGetData(this.videoPath.delPath, this.delVideoId).then(res => {
        this.getVideoDate()
      })
    },
    // 
    editVideoItem(row) {
      let t = Number(row.duration)
      let h = Math.floor((t / 3600) % 24)
      let m = Math.floor((t / 60) % 60)
      let s = Math.floor(t % 60)
      this.duration.hour = h
      this.duration.minute = m
      this.duration.second = s

      //
      baseService.basePostData(this.categoryPath.getPath).then(res => {
        let result = res.data.data
        this.categoryData = result
      })
      this.editVideoData = row
      this.editVideoDialogVisible = true
    },
    // 
    submitEditVideo() {
      this.editVideoData.duration = Number(this.duration.hour) * 60 * 60 + Number(this.duration.minute) * 60 + Number(this.duration.second)
      this.editVideoDialogVisible = false
      baseService.basePostData(this.videoPath.editPath, this.editVideoData).then(res => {
        this.getVideoDate()
      })
    },
    // 
    detailVideoItem(row) {
      this.$router.push(`/video/detail?id=${row.id}`)
    },
    // 
    handleClose() {
      this.addVideoDialogVisible = false
      this.editVideoDialogVisible = false
      this.delVideoDialogVisible = false
    },
    handleSuccessVideo(val) {
      this.addVideoData.videoToken = val.data.token
    },
    handleSuccessPic(val) {
      this.addVideoData.thumbToken = val.data.token
    },
    handleSuccessVideoEdit(val) {
      this.editVideoData.videoToken = val.data.token
    },
    handleSuccessPicEdit(val) {
      this.editVideoData.thumbToken = val.data.token
    }
  }
}
</script>

<style>
.add-video .el-select {
  width: 100%;
}
.view-container .el-upload__tip {
  line-height: 14px;
}
.video-druction .el-input {
  width: 40px;
}
.video-druction .el-input__inner {
  padding: 2px;
}
</style>




