<template>
  <div class="view-container">
    <!---->
    <div class="row breadcrumb-container">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>

    <div class="row ope-container">
      <div class="row base-search">
        <el-input size="small" v-model="search.id" placeholder="id"></el-input>
        <el-select size="small" v-model="search.subject" placeholder="">
          <el-option v-for="(item, index) in subjectList" :key="index" :label="item.name" :value="item.val">
          </el-option>
        </el-select>
        <el-select size="small" v-model="search.type" placeholder="">
          <el-option v-for="(item, index) in licenseList" :key="index" :label="item.name" :value="item.name">
          </el-option>
        </el-select>
        <el-input size="small" v-model="search.question" placeholder=""></el-input>
        <el-button size="small" type="primary" @click="getData()"></el-button>
        <el-button size="small" type="primary" @click="reset()"></el-button>
        <el-button size="small" type="primary" @click="add()"></el-button>
      </div>
      <div class="row senior-search">
        <el-date-picker size="small" v-model="creatTime" type="daterange" range-separator="" start-placeholder="" end-placeholder="" @change="creatTimeChange" value-format="timestamp">
        </el-date-picker>
        <el-date-picker size="small" v-model="updateTime" type="daterange" range-separator="" start-placeholder="" end-placeholder="" @change="updateTimeChange" value-format="timestamp">
        </el-date-picker>
      </div>
    </div>

    <!--  -->
    <div class="row data-container">
      <el-table :data="data" border style="width: 100%" :max-height="tabMaxHeight">
        <el-table-column label="id" prop="id">
        </el-table-column>
        <el-table-column label="" prop="phone">
          <template slot-scope="scope">
            <div v-if="scope.row.subject === '1'"></div>
            <div v-if="scope.row.subject === '4'"></div>
          </template>
        </el-table-column>
        <el-table-column label="" prop="type">
        </el-table-column>
        <el-table-column label="" prop="licenseType">
          <template slot-scope="scope">
            <el-select size="small" v-model="categoryIds[scope.$index]" placeholder="" @change="change(scope.$index,scope.row)">
              <el-option v-for="(item, index) in categoryDate" :key="index" :label="item.name" :value="item.id">
              </el-option>
            </el-select>
          </template>
        </el-table-column>
        <el-table-column label="" prop="answer">
        </el-table-column>
        <el-table-column label="" prop="createTime">
          <template slot-scope="scope">
            <div>{{moment(scope.row.createTime)}}</div>
          </template>
        </el-table-column>
        <el-table-column label="" prop="updateTime">
          <template slot-scope="scope">
            <div v-if="scope.row.updateTime">{{moment(scope.row.updateTime)}}</div>
          </template>
        </el-table-column>
        <el-table-column label="" width="300">
          <template slot-scope="scope">
            <el-button type="primary" size="mini" @click="detail(scope.row)">
              
            </el-button>
            <el-button type="primary" size="mini" @click="edit(scope.row)">
              
            </el-button>
            <el-button type="primary" size="mini" @click="del(scope.row)">
              
            </el-button>
          </template>
        </el-table-column>s
      </el-table>
    </div>
    <!--  -->
    <div class="row page-container">
      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="search.currentPage" :page-sizes="[10, 20]" :page-size="search.pageSize" layout="total, sizes, prev, pager, next, jumper" :total="totalSize">
      </el-pagination>
    </div>

    <!--  -->
    <el-dialog title="" :visible.sync="addDialogVisible" width="30%" :before-close="handleClose">
      <el-form class="add-form" label-position="left" label-width="0px" :model="addData" :rules="addRules" ref="addFrom">
        <el-form-item label="" prop="subject">
          <el-select size="small" v-model="addData.subject" placeholder="">
            <el-option v-for="(item, index) in subjectList" :key="index" :label="item.name" :value="item.val">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="type">
          <el-checkbox-group v-model="addData.type">
            <el-checkbox v-for="(item, index) in licenseList" :key="index" :label="item.name"></el-checkbox>
          </el-checkbox-group>
        </el-form-item>
        <el-form-item label="" prop="question">
          <el-input v-model="addData.question" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="item1">
          <el-input v-model="addData.item1" placeholder="A"></el-input>
        </el-form-item>
        <el-form-item label="" prop="item2">
          <el-input v-model="addData.item2" placeholder="B"></el-input>
        </el-form-item>
        <el-form-item label="" prop="item3">
          <el-input v-model="addData.item3" placeholder="C"></el-input>
        </el-form-item>
        <el-form-item label="" prop="item4">
          <el-input v-model="addData.item4" placeholder="D"></el-input>
        </el-form-item>
        <el-form-item label="" prop="answer">
          <el-select size="small" v-model="addData.answer" placeholder="">
            <el-option v-for="(item, index) in answerList" :key="index" :label="item" :value="item">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="explains">
          <el-input v-model="addData.explains" placeholder=""></el-input>
        </el-form-item>
        <el-form-item>
          <el-button size="small" @click="addDialogVisible = false"> </el-button>
          <el-button type="primary" size="small" @click="submitAdd('addFrom')"> </el-button>
        </el-form-item>
      </el-form>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="editDialogVisible" width="30%" :before-close="handleClose">
      <el-form class="add-form" label-position="left" label-width="0px" :model="editData" :rules="addRules" ref="editData">
        <el-form-item label="" prop="subject">
          <el-select size="small" v-model="editData.subject" placeholder="">
            <el-option v-for="(item, index) in subjectList" :key="index" :label="item.name" :value="item.val">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="type">
          <el-checkbox-group v-model="editData.type">
            <el-checkbox v-for="(item, index) in licenseList" :key="index" :label="item.name"></el-checkbox>
          </el-checkbox-group>
        </el-form-item>
        <el-form-item label="" prop="question">
          <el-input v-model="editData.question" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="item1">
          <el-input v-model="editData.item1" placeholder="A"></el-input>
        </el-form-item>
        <el-form-item label="" prop="item2">
          <el-input v-model="editData.item2" placeholder="B"></el-input>
        </el-form-item>
        <el-form-item label="" prop="item3">
          <el-input v-model="editData.item3" placeholder="C"></el-input>
        </el-form-item>
        <el-form-item label="" prop="item4">
          <el-input v-model="editData.item4" placeholder="D"></el-input>
        </el-form-item>
        <el-form-item label="" prop="answer">
          <el-select size="small" v-model="editData.answer" placeholder="">
            <el-option v-for="(item, index) in answerList" :key="index" :label="item" :value="item">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="explains">
          <el-input v-model="editData.explains" placeholder=""></el-input>
        </el-form-item>
        <el-form-item>
          <el-button size="small" @click="addDialogVisible = false"> </el-button>
          <el-button type="primary" size="small" @click="submitEdit('editData')"> </el-button>
        </el-form-item>
      </el-form>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="delDialogVisible" width="30%" :before-close="handleClose">
      <span></span>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" @click="delDialogVisible = false"> </el-button>
        <el-button type="primary" size="small" @click="submitDel()"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import baseService from '../../service/baseService.js'
import moment from 'moment'
export default {
  name: '',
  data() {
    return {
      // api
      apiPath: {
        get: '/question/page',
        add: '/question/add',
        edit: '/question/update',
        del: '/question/delete'
      },
      // 
      data: [],
      //   
      search: {
        id: '',
        subject: '',
        type: '',
        question: '',
        creatBeginDate: '',
        creatEndDate: '',
        updateBeginDate: '',
        updateEndDate: ''
      },
      totalSize: 0,
      //   
      addDialogVisible: false,
      //   
      addData: {
        subject: '',
        type: [],
        question: '',
        item1: '',
        item2: '',
        item3: '',
        item4: '',
        answer: '',
        explains: ''
      },
      // 
      addRules: {
        subject: [{ required: true, message: '', trigger: 'change' }],
        type: [{ required: true, message: '', trigger: 'change' }],
        question: [{ required: true, message: '', trigger: 'change' }],
        item1: [{ required: true, message: 'A', trigger: 'change' }],
        item2: [{ required: true, message: 'B', trigger: 'change' }],
        answer: [{ required: true, message: '', trigger: 'change' }],
        explains: [{ required: true, message: '', trigger: 'change' }]
      },
      // 
      editDialogVisible: false,
      // 
      editData: {
        id: '',
        subject: '',
        type: [],
        question: '',
        item1: '',
        item2: '',
        item3: '',
        item4: '',
        answer: '',
        explains: '',
        categoryIds: ''
      },
      // 
      subjectList: [
        {
          name: '',
          val: 1
        },
        {
          name: '',
          val: 4
        }
      ],
      // 
      answerList: ['A', 'B', 'C', 'D'],
      tabMaxHeight: 0,
      // 
      delDialogVisible: false,
      // id
      delId: '',
      // 
      categoryDate: [],
      categoryPath: {
        getPath: '/category/question'
      },
      categoryIds: [],
      creatTime: '',
      updateTime: ''
    }
  },
  computed: {
    licenseList() {
      return this.$store.state.data.licenseList
    }
  },
  mounted() {
    // 
    this.getData()
  },
  methods: {
    // 
    moment(time) {
      return moment(time).format('YYYY-MM-DD h:mm:ss')
    },
    // 
    getData() {
      baseService.basePostData(this.apiPath.get, this.search).then(res => {
        let result = res.data
        this.data = result.data.list
        this.totalSize = result.data.total
        this.categoryIds = []
        for (let i = 0; i < this.data.length; i++) {
          this.categoryIds.push(this.data[i].categoryId)
        }
        let clientHieght = document.body.clientHeight
        this.tabMaxHeight = clientHieght - 60 - 30 - 117 - 50 - 50
        if (result.status !== '0x0000') {
          this.$message({
            message: result.message,
            type: 'warning'
          })
        }
      })

      baseService.basePostData(this.categoryPath.getPath, {}).then(res => {
        let result = res.data.data
        this.categoryDate = result
      })
    },
    // 
    reset() {
      this.search = {
        id: '',
        subject: '',
        type: '',
        question: '',
        creatBeginDate: '',
        creatEndDate: '',
        updateBeginDate: '',
        updateEndDate: ''
      }
      this.getData()
    },
    // 
    add() {
      this.addDialogVisible = true
    },
    // 
    submitAdd(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.addDialogVisible = false
          baseService.basePostData(this.apiPath.add, this.addData).then(res => {
            let result = res.data
            if (result.status !== '0x0000') {
              this.$message({
                message: result.message,
                type: 'warning'
              })
            }
            this.getData()
          })
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    // 
    edit(row) {
      this.editData = row
      this.editDialogVisible = true
    },
    // 
    submitEdit(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.editDialogVisible = false
          baseService.basePostData(this.apiPath.edit, this.editData).then(res => {
            let result = res.data
            if (result.status !== '0x0000') {
              this.$message({
                message: result.message,
                type: 'warning'
              })
            }
            this.getData()
          })
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    // 
    del(row) {
      this.delId = row.id
      this.delDialogVisible = true
    },
    // 
    submitDel() {
      this.delDialogVisible = false
      baseService.baseGetData(this.apiPath.del, this.delId).then(res => {
        let result = res.data
        if (result.status !== '0x0000') {
          this.$message({
            message: result.message,
            type: 'warning'
          })
        }
        this.getData()
      })
    },
    // 
    change(index, row) {
      this.editData = row
      this.editData.categoryId = this.categoryIds[index]
      baseService.basePostData(this.apiPath.edit, this.editData).then(res => {
        let result = res.data
        if (result.status !== '0x0000') {
          this.$message({
            message: result.message,
            type: 'warning'
          })
        }
        this.getData()
      })
    },
    handleClose() {
      this.addDialogVisible = false
      this.editDialogVisible = false
      this.delDialogVisible = false
    },
    // ----------------------------------------------------
    handleSizeChange(val) {
      this.search.pageSize = val
      this.getData()
    },
    handleCurrentChange(val) {
      this.search.currentPage = val
      this.getData()
    },
    creatTimeChange(val) {
      this.search.creatBeginDate = val[0]
      this.search.creatEndDate = val[1]
    },
    updateTimeChange(val) {
      this.search.updateBeginDate = val[0]
      this.search.updateEndDate = val[1]
    }
  }
}
</script>
<style>
.base-search .el-input {
  width: 150px;
}
.senior-search .el-input {
  width: 200px;
}
</style>




